<template>
<v-container style="padding-left:0;padding-right:0;padding-top:0">
     <v-layout row>  
       <v-flex 12xs>       
        <v-expansion-panel  >
             <v-expansion-panel-content
      v-for="(item,i) in items"  
      :key="i"    
     
    >       
            
            <v-divider v-if="index + 1 < items.length" :key="`divider-${index}`"></v-divider>
      <v-list two-line slot="header"  style="padding:0">
        <v-list-tile color="black">
          <v-list-tile-avatar ><img :src="item.image"></v-list-tile-avatar>
          <v-list-tile-content>{{item.text}}</v-list-tile-content>  
          <v-list-tile-action-text v-if="containsEuro(item.price)" ><v-icon>attach_money</v-icon></v-list-tile-action-text>        
        </v-list-tile>
       
       
      </v-list>
      <v-card>
        <v-card-text v-if="containsEuro(item.price)">{{item.price}}</v-card-text>
         <v-btn v-if="containsEuro(item.price)" flat @click="buyTicket(item)">Ticket kaufen</v-btn>
         <v-btn v-if="!containsEuro(item.price)" flat>Anmelden</v-btn>
      </v-card>
            
            
             </v-expansion-panel-content>
        </v-expansion-panel>
       </v-flex>
     </v-layout>
 </v-container>
</template>

<script>
export default {
  methods:{
    containsEuro(price){
      if(price != null && price.includes("€"))
        return true;
      else 
        return false;
        
    },
    cutAfter15(text){
        return text.substring(0,15)
    },
    buyTicket(item){
       this.$store.dispatch('Payment/buyTicket', item)
       .then(() => {
          this.$router.push('/')
       });
       
    }
  },
  computed:{
      items(){
          return this.$store.getters['Events/getEvents']
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.list-hotfix {
    flex: 1 1 auto !important;
    overflow: hidden;
}
h3 {
  margin: 40px 0 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>
